'use strict';

/**
* Returns `y`.
*
* @private
* @param {number} x - first number
* @param {number} y - second number
* @returns {number} second number
*
* @example
* var z = f( 5.0, 3.14 );
* // returns 3.14
*/
function f( x, y ) {
	return y;
}


// EXPORTS //

export default f;
